.nav-title {
    font-size: 5vw;
    font-weight: 700;
    display: inline-block;
    color: #f2f2f2;
    padding-left: 2vw;
    margin: auto;
    padding-top: 1vh
    /* font-family: ; */
}

.nav {
  overflow: hidden;
  background-color: royalblue;
  text-align: left;
  padding-bottom: 1vh;
  height: 15vh;
}

.nav-link {
  color: black;
  text-align: center;
  text-decoration: none;
  font-size: 15px;

  /* these last two make the logout button in
  the nav bar look like another link */
  border:none;
  cursor: pointer;
  outline:0;
}

.loginView {
    text-align: center;
    justify-content: center;
    margin: 30;
    padding: 20;
}

.loginHeader {
    text-align: center;
    font-size: 32;
}

.loginInput input::-webkit-input-placeholder {
    text-align: center;
}

.loginInput input::-ms-input-placeholder {
    text-align: center;
}

.loginInput input::placeholder {
    text-align: center;
}
body {
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    margin: 0px;
}

.alert {
    background-color: #ff4545;
    margin: 50px;
}

