input {
    margin: 5px;
    margin-bottom: 10px;
}

.container {
    background-color: #eeeeee;
    width: 1080;
    margin: auto;
}

.log-in, .log-out, .register {
  /* background-color: #00acb0; */
  border-radius: 4px;
  color: black;
  min-height: 50px;
  min-width: 100px;
  outline:0;
  cursor: pointer;
}

.log-in:hover, .log-out:hover, .register:hover {
  background-color: #008183;
}

/* This line makes the "Register" and "Login" buttons look like links */
.link-button {
    background:none;
    color:inherit;
    border:none;
    padding:0;
    font: inherit;
    outline:0;
    border-bottom:1px solid #444; 
    cursor: pointer;
}

